/* Page background and container */

.employee-container {
    height: 100vh;
    background: linear-gradient(120deg, #ffecd2 0%, #fcb69f 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}


/* Card styles */

.employee-card {
    background: white;
    padding: 40px 30px;
    border-radius: 16px;
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 400px;
    transition: transform 0.3s ease;
}

.employee-card:hover {
    transform: translateY(-5px);
}


/* Title */

.employee-title {
    text-align: center;
    margin-bottom: 25px;
    color: #333;
    font-size: 24px;
}


/* Inputs */

.employee-input {
    width: 100%;
    padding: 12px;
    margin-bottom: 18px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 15px;
    transition: 0.3s;
}

.employee-input:focus {
    border-color: #fcb69f;
    outline: none;
    box-shadow: 0 0 5px rgba(252, 182, 159, 0.6);
}


/* Button */

.employee-button {
    width: 100%;
    padding: 14px;
    background: #f77f00;
    color: rgb(165, 16, 16);
    font-weight: bold;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    cursor: pointer;
    transition: background 0.3s, transform 0.2s;
}

.employee-button:hover {
    background: #d95f00;
    transform: scale(1.03);
}


/* Message */

.employee-message {
    margin-top: 20px;
    font-weight: bold;
    text-align: center;
}

.employee-message.success {
    color: green;
}

.employee-message.error {
    color: red;
}